.animation-images {
  position: relative;
  width: 62.5vw;
  height: 37.031vw;
  flex-shrink: 0;
  &:hover {
    .animation-img--off {
      transform: scale(1.2);
    }

    .animation-img--on {
      opacity: 1;
      visibility: visible;
      transform: scale(1.2);
    }
  }
}

.animation-img-off--active {
  transform: scale(1.2);
}

.animation-img-on--active {
  opacity: 1 !important;
  visibility: visible !important;
  transform: scale(1.2);
}

.animation-img--off {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 100%;
  height: 100%;
  transition: all 1.5s ease-out;
  z-index: 1;
}
.animation-img--on {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 1.5s ease-out;
  z-index: 2;
}
