  border-radius: 50%;
  z-index: 3;
}
.geography-marker__item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 1px solid var(--red-color);
  background: #1c1c1e;
  border-radius: 50%;
  animation: marker 3s linear infinite;
  animation-direction: alternate;
  z-index: 2;
}
.geography-marker::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: marker-shadow-flash 3s linear infinite;
}
.geography-marker::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: marker-shadow 3s ease-out infinite;
