.work {
  position: relative;
  padding-top: 1.25vw;
  margin-bottom: 6.25vw;
}
.work-container {
  display: flex;
  flex-direction: column;
}

.work-top {
  display: flex;
  justify-content: space-between;
}
.work-content {
  max-width: 41.667vw;

  .btn {
    width: 20.833vw;
  }
}
.work-title {
  font-family: var(--font-family-Europe);
  font-weight: 400;
  font-size: 2.396vw;
  line-height: 3.438vw;
  text-transform: uppercase;
  margin-bottom: 1.563vw;
}
.work-text {
  display: flex;
  flex-direction: column;
  gap: 0.781vw;
  margin-bottom: 2.604vw;
  p {
    font-weight: 300;
    font-size: 1.042vw;
    line-height: 1.563vw;
    opacity: 0.7;
    span {
      font-weight: 700;
    }
  }
}
.work-images {
  top: 3vw;
  right: 5vw;
  width: 42.656vw;
  height: 34.375vw;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  z-index: 0;
  img {
    width: 30.156vw;
    height: 29.323vw;
    top: 0;
    left: 0;
  }
}

.work-anim {
  position: absolute;
  top: -7vw;
  right: 5vw;
  width: 100%;
  height: 40vw;
  transform: rotate(180deg);
  z-index: -1;
}

.work-anim__horizontal {
  display: block;
  margin: 6.25vw 0;
  width: 100%;
  height: 5.208vw;
}

.work-documents {
  display: flex;
  flex-direction: column;
  gap: 2.604vw;

  .btn {
    svg {
      transform: translateY(-50%) rotate(0);
    }
  }
}

.work-documents__title {
  font-family: var(--font-family-Europe);
  font-weight: 400;
  font-size: 1.875vw;
  line-height: 2.083vw;
}

.work-documents__inner {
  display: flex;
  gap: 1.042vw;
}

//count
.count {
  position: relative;
  margin-bottom: 4vw;
  display: flex;
  flex-direction: column;
  gap: 2vw;
  background-color: var(--bg-color);
  z-index: 1;
}
.count-title {
  font-family: var(--font-family-Europe);
  font-weight: 400;
  font-size: 1.875vw;
  line-height: 2.083vw;
}
.count-items {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
.count-item {
  display: flex;
  flex-direction: column;
  gap: 1.875vw;
}
.count-num {
  display: block;
  font-weight: 800;
  font-size: 3.646vw;
  line-height: 1.563vw;
  color: transparent;
  -webkit-text-stroke: 1px var(--red-color);
}

.count-text {
  max-width: 18.854vw;
  font-weight: 300;
  font-size: 1.042vw;
  line-height: 1.563vw;
}

.work {
  .title-page {
    h2 {
      text-transform: uppercase;
      font-size: 2.396vw;
      line-height: 3.438vw;
    }
  }
}

.export {
  .work-container {
    gap: 3.9vw;
  }

  .work-images {
    width: 46.563vw;
    height: 27.604vw;
    right: -8vw;
    top: 3vw;
    transform: matrix(1, 0, 0, 1, 0, 0);
    img {
      width: 46.563vw;
      height: 27.604vw;
      top: 0;
      left: 0;
    }
  }

  .work-anim {
    position: absolute;
    top: -5vw;
    right: 2vw;
    width: 100%;
    height: 40vw;
    transform: rotate(0);
  }

  .work-anim__horizontal {
    margin-top: 2vw;
    margin-bottom: 6vw;
  }
}

.transshipment-oil {
  .work-images {
    right: -10vw;
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .work-anim {
    top: -7vw;
    right: 10vw;
    transform: rotate(0);
  }

  .work-anim__horizontal {
    margin: 0vw 0 9vw;
  }
}

.transshipment-bitumen {
  .work-container {
    gap: 4vw;
  }
  .work-images {
    right: -11vw;
    top: 3vw;
    transform: matrix(1, 0, 0, 1, 0, 0);
    img {
      width: 35.99vw;
      height: auto;
    }
  }

  .work-anim {
    top: -6vw;
    right: 7vw;
    transform: rotate(0);
  }

  .work-anim__horizontal {
    margin: 0vw 0 8vw;
  }
}

.illustration {
  .work-container {
    gap: 2vw;
  }

  .illustration-img {
    position: relative;
    top: 7vw;
    right: 10vw;
    width: 16.198vw;
    height: 16.198vw;
    &::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: -3vw;
      transform: translateX(-50%);
      width: 16.198vw;
      height: 2.969vw;
      background: #000000;
      border-radius: 50%;
      opacity: 0.26;
      filter: blur(20.5674px);
    }
    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  .work-anim {
    top: -7vw;
    right: -17vw;
    width: 53vw;
    height: 32vw;
    transform: rotate(-360deg);
  }
}

.bunkering {
  .work-text {
    margin-bottom: 5vw;
  }

  .work-images {
    top: 0;
    right: -5vw;
    img {
      width: 37.969vw;
      height: auto;
    }
  }

  .work-anim {
    top: -3vw;
    right: 0;
  }

  .work-anim__horizontal {
    margin: 0vw 0 8vw;
  }
}

.earth {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 27vw;
  height: auto;
  opacity: 0.7;
}

.earth-path {
  position: absolute;
  display: block;
  width: 10px;
  height: 10px;
  background-color: red;
  z-index: 2;
  &--1 {
    top: 8.7vw;
    left: -2vw;
    background-color: var(--bg-color);
  }
  &--2 {
    top: 12vw;
    left: 1.4vw;
    background-color: #000000;
  }
  &--3 {
    top: 10.8vw;
    right: 0.6vw;
    background-color: #000000;
  }
}

